import Vue from 'vue'

export default {

    actions: {
        loadData ({ commit }) {
            Vue.prototype.$http('data.json').then(resp => {
                const data = resp.data
                if (data) {
                    commit('setStocks', data.stocks)
                    commit('setPortfolio', {
                        stockPortfolio: data.stockPortfolio,
                        funds: data.funds
                    })
                }
            })
        }
    }
}